import React, { useEffect, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../../app/reducer'
import { setIsVideo } from '../playerSlice'
import ReactPlayer from 'react-player'

const VideoPlayer = () => {
  const videoPlayerRef = useRef(null)

  const dispatch = useDispatch()

  const currentTime = useSelector(
    (state: RootState) => state.player.currentTime
  )

  useEffect(() => {
    if (videoPlayerRef.current) {
      // dispatch(setVideoPlayerRef(videoPlayerRef.current))
      console.log(videoPlayerRef.current)
      dispatch(setIsVideo(true))

      return () => {
        dispatch(setIsVideo(false))
      }
    }
  }, [videoPlayerRef, dispatch])

  useEffect(() => {
    const player = document.getElementById('player')
    let timerId: NodeJS.Timeout | null = null
    if (videoPlayerRef.current) {
      timerId = setInterval(() => {
        let diff = Math.abs(
          // @ts-ignore
          player.currentTime - videoPlayerRef.current.getCurrentTime()
        )
        if (diff > 0.5) {
          // @ts-ignore
          videoPlayerRef.current.seekTo(player.currentTime + 0.5, 'seconds')
        }
      }, 1000)
    }
    return () => {
      if (timerId) {
        clearInterval(timerId)
      }
    }
  }, [videoPlayerRef])

  // useEffect(() => {
  //   if (videoPlayerRef.current && currentTime) {
  //     // @ts-ignore
  //     videoPlayerRef.current.seekTo(currentTime, 'seconds')
  //   }
  // }, [currentTime])

  const currentAudioID = useSelector(
    (state: RootState) =>
      'https://www.youtube.com/watch?v=' + state.player.currentAudio.id
  )
  // const volume = useSelector((state: RootState) => state.player.volume)
  const isPlaying = useSelector((state: RootState) => state.player.isPlaying)
  return (
    <ReactPlayer
      controls={false}
      pip={true}
      id={'VideoPlayer'}
      ref={videoPlayerRef}
      url={currentAudioID}
      style={{
        height: '100%',
        maxWidth: '1050px',
        flex: '1 0 auto',
        position: 'relative',
      }}
      width="100%"
      height="100%"
      volume={0}
      playing={isPlaying}
    />
  )
}

export default VideoPlayer
