import React from 'react'
import { Col, Row } from 'antd'
import styles from './HomeContent.module.css'
import { RootState } from '../../app/reducer'
import { useDispatch, useSelector } from 'react-redux'
import { Audio } from '../player/types'
import { setAudio } from '../player/playerSlice'
import clsx from 'clsx'

const HomeContent = () => {
  const currentPlaylist = useSelector(
    (state: RootState) => state.player.playlist.audios
  )
  const currentAudio = useSelector(
    (state: RootState) => state.player.currentAudio
  )

  const isPlaying = useSelector((state: RootState) => state.player.isPlaying)

  const dispatch = useDispatch()

  const onAudioClick = (audio: Audio) => {
    dispatch(setAudio(audio))
  }

  return (
    <Row justify="center">
      <Col xs={24} md={24} lg={24} xl={24}>
        <Row style={{ padding: '25px' }}>
          {currentPlaylist.map((el) => (
            <Col
              style={{ color: 'rgb(179, 179, 179)' }}
              xs={12}
              md={8}
              lg={6}
              xxl={4}
              key={el.id}
            >
              <Image
                audio={el}
                onClick={onAudioClick}
                isPlaying={isPlaying}
                isCurrent={el.id === currentAudio.id}
              />
              <div
                // className={'ellipsis-one-line'}
                style={{ paddingLeft: '10px', paddingRight: '10px' }}
              >
                {el.name}
              </div>
            </Col>
          ))}
        </Row>
      </Col>
    </Row>
  )
}

type ImageProps = {
  audio: Audio
  onClick: (audio: Audio) => void
  isPlaying: boolean
  isCurrent: boolean
}
const Image = ({ audio, onClick, isPlaying, isCurrent }: ImageProps) => {
  const onButtonClick = () => {
    onClick(audio)
  }

  let wrapperClasses = [styles.ImageWrapper]
  if (isCurrent && isPlaying) {
    wrapperClasses = [...wrapperClasses, styles.ImageWrapperActive]
  }

  let ImageClasses = [styles.Image]
  if (isCurrent && isPlaying) {
    ImageClasses = [...ImageClasses, styles.ImageActive]
  }

  let ButtonClasses = [styles.PlayButton]
  if (isCurrent && isPlaying) {
    ButtonClasses = [...ButtonClasses, styles.PlayButtonActive]
  }

  const Emoji = (
    <span role="img" aria-label="Sheep" style={{ fontSize: '40px' }}>
      🦊
    </span>
  )

  return (
    <div style={{ padding: '10px', overflow: 'hidden' }}>
      <div className={clsx(wrapperClasses)}>
        <div className={clsx(ButtonClasses)} onClick={onButtonClick}>
          <div style={{ userSelect: 'none' }}>
            {isPlaying && isCurrent ? (
              <img src={require('./LissNight.png')} width={50} height={50} />
            ) : (
              '|>'
            )}
          </div>
        </div>
        <img
          alt={''}
          className={clsx(ImageClasses)}
          src={audio.thumbnail}
          draggable={false}
        />
      </div>
    </div>
  )
}

export default HomeContent
