import styles from './Content.module.css'
import React, { useEffect, useState } from 'react'
import { Layout } from 'antd'
import ContentSwitcher from '../router/ContentSwitcher'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../app/reducer'
import { searchNext } from '../search/searchSlice'
import { useLocation } from 'react-router-dom'

const Content = () => {
  let location = useLocation()
  const dispatch = useDispatch()
  const nextPageUrl = useSelector(
    (state: RootState) => state.search.searchResult.nextPageUrl
  )

  const isFetching = useSelector((state: RootState) => state.search.isFetching)

  useEffect(() => {
    if (location.pathname.startsWith('/search')) {
      let timer: number | undefined

      const el = document.getElementById('scrollContainer')
      if (el) {
        el.onscroll = (event) => {
          if (timer) {
            window.clearTimeout(timer)
          }

          timer = window.setTimeout(function () {
            // console.log(event)
            // console.log(el.clientHeight, el.offsetHeight, el.scrollHeight)
            // console.log(el.scrollTop)

            // Preload music
            const spacePreload = 50

            const toBottom = el.scrollHeight - el.clientHeight - el.scrollTop
            // console.log(toBottom)
            if (toBottom < spacePreload) {
              if (nextPageUrl && !isFetching) {
                dispatch(searchNext(nextPageUrl))
              }
            }
          }, 100)
        }
      }
    }
  }, [nextPageUrl, isFetching, location.pathname])

  return (
    <Layout.Content className={styles.Content} id={'scrollContainer'}>
      <ContentSwitcher />
    </Layout.Content>
  )
}

export default Content
